import React from 'react'
import { BrowserRouter, Routes,  Link, HashRouter } from 'react-router-dom'


let Home = (props: any) => (
  <div>
    <h1>Home</h1>
    <nav>
      <Link to={'/'}>Home</Link> |{" "}
      <Link to={'dashboard'}>Dashboard</Link>
    </nav>
  </div>
)

let Dash = (props: any) => <div>Dash</div>

let Header = () => (
  <div>Test</div>
)

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes basename={process.env.PUBLIC_URL}>
          <Header/>
        </Routes>
        <Routes basename={process.env.PUBLIC_URL}>
          <Home path={'/'} />
          <Dash path={'dashboard'} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
